.fade-appear{
    opacity:0;
}

.fade-appear.fade-appear-active{
    opacity: 1;
    transition: opacity 1000ms linear 300ms;
}

.fadeout-leave{
    opacity: 1;
}

.fadeout-leave.fadeout-leave-active{
    opacity: 0;
    transition: opacity 300ms linear;
}